    font-family: "GTEestiProDisplay-Light";
    src: local("GTEestiProDisplay-Light"), url("./fonts/gt-eesti-pro-display-light.otf") format("truetype");
}
@font-face {
    font-family: "GTEestiProDisplay-Thin";
    src: local("GTEestiProDisplay-Thin"), url("./fonts/gt-eesti-pro-display-thin.otf") format("truetype");
}
